.modal{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background-color:rgb(245, 242, 242);
    border: 1px solid #000;
    z-index: 3;
    width: 540px;
}
.button{
    position: absolute;
    box-shadow: 7px 10px 30px -2px rgba(0,0,0,0.74);
    -webkit-box-shadow: 7px 10px 30px -2px rgba(0,0,0,0.74);
    -moz-box-shadow: 7px 10px 30px -2px rgba(0,0,0,0.74);
    top: 5px;
    right: 5px
}

.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}